<template>
   <section id="charge">
      <span class="title">{{$t('chargeMoney.chargeMoney')}}</span>
      <el-col :span="24" style="padding-bottom: 0px" class="toolbar">
        <el-form :inline="true" :model="userForm" ref="userForm">
        	<el-form-item :label="$t('coins')" prop="coins">
        		<el-select v-model="userForm.coins" :placeholder="$t('All')">
			      <el-option label="USDG" value="USDG"></el-option>
			      <el-option label="BTY" value="BTY"></el-option>
			    </el-select>
        	</el-form-item>
            <el-form-item  prop="startTime">
                <el-date-picker type="date" :placeholder="$t('CS')" v-model="userForm.startTime"></el-date-picker>              
            </el-form-item>
            <el-form-item :label="$t('go')" prop="endTime">
                <el-date-picker type="date" :placeholder="$t('CE')" v-model="userForm.endTime"></el-date-picker>
            </el-form-item>
            <el-form-item prop="enterpriseName">
                <el-input :placeholder="$t('pleaseEnter')" v-model="input" class="input-with-select">
                  <el-select v-model="select" slot="prepend" :placeholder="$t('Pchoose')">
                    <el-option label="UID" value="uid"></el-option>
                    <el-option :label="$t('userName')" value="personName"></el-option>
                  </el-select> 
                </el-input>
            </el-form-item>
            <el-form-item>
              <el-button class="warning" @click="getUsers">{{$t('search')}}</el-button>
            </el-form-item>
            <el-form-item>
              <!-- <el-button class="info" v-on:click="handleReset('userForm')">{{$t('reset')}}</el-button>                  -->
              <el-button class="info" @click="handleReset">{{$t('reset')}}</el-button>                 
            </el-form-item>
        </el-form>
      </el-col>

      <el-table :data="userList"   v-loading="userLoading" style="width: 100%;" :row-class-name="tableRowClassName" >
           <el-table-column prop="id"  :label="$t('chargeMoney.tableHead[0]')">
            </el-table-column>
            <el-table-column prop="uid" :label="$t('chargeMoney.tableHead[1]')">
            </el-table-column>
            <el-table-column prop="personName"  :label="$t('chargeMoney.tableHead[2]')">
            </el-table-column>
            <el-table-column prop="coinType"  :label="$t('chargeMoney.tableHead[3]')">
            </el-table-column>
            <el-table-column prop="coinAmount" :label="$t('chargeMoney.tableHead[4]')">
            </el-table-column>
            <el-table-column prop="utime"  :label="$t('chargeMoney.tableHead[5]')">
            </el-table-column>
           <!--  <el-table-column prop="chuShen" :label="$t('chargeMoney.tableHead[6]')">
            </el-table-column>
            <el-table-column prop="fuShen"  :label="$t('chargeMoney.tableHead[7]')">
            </el-table-column> -->
            <el-table-column prop="state"  :label="$t('chargeMoney.tableHead[8]')">
              <template slot-scope="scope">
                <span>{{$t('success')}}</span>
              </template>
            </el-table-column>
      </el-table>
      <div class="page" v-show="total >10 ">
      <el-col :span="24" class="toolbar  ">
         <el-pagination
          background
          layout="prev, pager, next"
          :total="total" @current-change="handleCurrentChange">
        </el-pagination>
      </el-col>
      </div>
   </section>
</template>
   
<script>
import {ajax,dateStr2GmtTimeStamp,getDataYear,dateStrSecond} from '../../common/js/common.js'

export default {
   data(){
     return{
      input:"",
       select:"",
       startT:"",
       endT:"",
       userForm:{
   		   coins:'',
         startTime:'',
         endTime:'',
         enterpriseName:''
       },
       userLoading:false,
       userList:[],
       rechargeDate:{
        page:1,
        rowNum:"10",

       },
       total:0,
     }
   },
   methods:{
    // 查询前台用户的冲币记录2
    queryFrontUserRecharge:function(){

       if(this.select != ""){
           this.userForm[this.select] = this.input;
         }
         if(this.userForm.startTime != ""){
          //  this.userForm.startTime = dateStr2GmtTimeStamp(this.userForm.startTime);
          let startTimeC = dateStr2GmtTimeStamp(this.userForm.startTime);
           // 获取秒的时间戳用于给后台传数据
           this.startT = dateStrSecond(startTimeC)
         }
         if(this.userForm.endTime != ""){
          //  this.userForm.endTime = dateStr2GmtTimeStamp(this.userForm.endTime);
          let endTimeC = dateStr2GmtTimeStamp(this.userForm.endTime);
           this.endT=dateStrSecond(endTimeC)
         }
         if(this.userForm.coins != ""){
          this.userForm.coins = this.userForm.coins;
         }

      let  data = {
          page:this.rechargeDate.page,
          rowNum:10,
          uid:"",
          personName:"",
          startTime:this.startT,
          endTime:this.endT,
          coinType:this.userForm.coins,
        
         }

           if(this.select=="uid"){
          data.uid = this.input;
         }else if(this.select == "personName"){
          data.personName = this.input;
        }

         if(data.coinType == "USDG"){
          data.coinType=1
         }else if(data.coinType == "BTY"){
          data.coinType=2
         }
        // console.log("data.coinType:",data.coinType)
        // console.log("什么情况")
        // console.log(data)
      ajax(this,this.ApiConfig.assetManagement.queryFrontUserRecharge,data,(res) => {
        if(res.code == 200){
          // console.log(res)
          this.userList = res.data.rows;
          let arr = this.userList;
          arr.forEach((item,i,arr)=>{
            arr[i].coinType= arr[i].coinType==1?"USDG":"BTY";
            arr[i].ctime = getDataYear('Y-M-D h:m:s',arr[i].ctime);
            arr[i].utime = getDataYear('Y-M-D h:m:s',arr[i].utime);
          })
          this.total =res.data.total;
          this.startT ="";
          this.endT = "";
        }else{
          this.$notify({
            title:"提示",
            message:res.message,
            type:"warning"
          })
          return false;
        }


        
      },"POST")
    },
     getUsers:function(){
        this.queryFrontUserRecharge();
     },
     handleReset:function(){
       
          // this.$refs[formName].resetFields();
          this.userForm.startTime="";
          this.userForm.endTime="";
          this.input="";
          this.select="";
          this.userForm.coins="";
          this.rechargeDate.page = 1;
          this.queryFrontUserRecharge();

     },
     tableRowClassName:function({row, rowIndex}){
         if(rowIndex % 2 == 1){
            return "success-row"
         }
     },
      handleCurrentChange:function(val){
       this.rechargeDate.page = val;
       this.queryFrontUserRecharge();
     },

   },
   mounted:function(){
    this.queryFrontUserRecharge();
   }
}
</script>

<style>
#charge{
  .el-select .el-input {
    width: 105px;
  }
}
</style>



